<template>
  <div>
    <div>
        <b-row v-b-toggle.sidebar-1 class="sidebar_btn">
            <b-col><span class="sidebar_btn_text">Click to expand</span><svg xmlns="http://www.w3.org/2000/svg"  width="30" height="30" fill="currentColor" class="bi bi-caret-right-fill sidebar_btn_icon" viewBox="0 0 16 16">
  <path d="M12.14 8.753l-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"/>
</svg></b-col>
        </b-row>
      <b-sidebar
        id="sidebar-1"
        title="My actions"
        sidebar-class="submenu-title"
      >
        <div class="px-3 py-2 my-3">
          <div>
            <b-nav vertical variant="dark" class="w-100 submenu">
              <b-nav-item class="submenu-link border-top border-dark" to="/candidate/profile" active>Profile</b-nav-item>
              <b-nav-item to="/candidate" class="submenu-link" >Lectures</b-nav-item>
              <b-nav-item class="submenu-link">Livestream</b-nav-item>
              <b-nav-item class="submenu-link">Quiz</b-nav-item>
            </b-nav>
          </div>
        </div>
      </b-sidebar>
    </div>
    <router-view v-if="authenticated"></router-view>
  </div>
</template>
<script>
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
  name:"Candidate",
  data(){
    return{
      authenticated:false,
      BASE_URL:"/"
    }
  },
  mounted:function(){
    if(this.$cookies.isKey('authtoken')){ 
      let authtoken=this.$cookies.get("authtoken");
      let vm=this;
      axios.get(this.BASE_URL+'checksession',{params:{'authtoken':authtoken}}).then(res=>{
        if(res.data.Error){
          this.$router.push('/');
        }
        else{
          if(res.data.user!='candidate'){
            this.$router.push('/');
          }
            vm.authenticated=true;
        }
    })}
  }
};
</script>
<style lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
.sidebar_btn{
    background: black;
    color: white;
    padding:10px 4px;
    .sidebar_btn_text{
      margin-left:20px;
      font-size: 1.2rem;
    }
    .sidebar_btn_icon{
        margin:0px 10px; 
    }
}
.submenu-title,
#sidebar-1{
    color:#556EE6 !important;

}
.b-sidebar-header{
    font-family: 'Playfair Display', serif !important;
    font-weight: 800;
    border-bottom: 3px solid #556EE6 !important;
}
.submenu{
    color:black;
    a{
        color: black !important;
    }
    .submenu-link{
        font-size: 1.2rem;
        border-bottom: 1px solid black;
        &:hover{
            background: white;
            border-left: 10px solid #556EE6  !important;
        }
    }
}
</style>