<template>
  <div id="login">
      <div class="form_container">
          <h3>Welcome</h3>
    <b-form @submit="login">
      <b-form-group
        id="username-group"
        label="Username:"
        label-for="username"
      >
        <b-form-input
          id="username"
          v-model="formdata.username"
          placeholder="Enter username(phone)"
          required
        ></b-form-input>
      </b-form-group>
      <b-form-group
        id="password-group"
        label="Password:"
        label-for="password"
      >
        <b-form-input
          id="password"
          type="password"
          v-model="formdata.password"
          placeholder="Enter password"
          required
        ></b-form-input>
      </b-form-group>
      <b-form-group class="button">
          <b-button pill variant="secondary" @click="$emit('login_cancel')" class="mr-3 px-3">Cancel</b-button>
          <b-button pill variant="success" type="submit" class="ml-3 px-3" @click="$emit('login_cancel')">Login</b-button>
      </b-form-group>
    </b-form>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
    name:"Login",
    data(){
        return {
            BASE_URL:"/",
            formdata:{username:null, password:null}
        }
    },
    props:['link'],
    methods:{
        login:function(e){
            e.preventDefault();
            let vm=this;
            axios.post(this.BASE_URL+this.$props.link+'/login',{phone:this.formdata.username,password:this.formdata.password}).then(res=>{
              if(res.data.Error){
                window.alert(res.data.Error);
              }
              else{
                let authtoken=res.data.authtoken;
                vm.$cookies.set("authtoken",authtoken,60*60*24*30);
                vm.$emit('loggedin');
              }
            })
        }
    }
}
</script>

<style scoped lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@600&display=swap');
#login{
    z-index: 1000;
    position: fixed;
    top:0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    .form_container{
        max-width: 800px;
        width: 100vw;
        min-width: 300px;
        background: white;
        padding: 20px 40px;
        box-shadow: 0px 0px 10px 2px white;
        h3{
            text-align: center;
            font-size: 2rem;
            font-family: 'Playfair Display', serif;
            font-weight: 800;
            border-bottom: 2px solid black;
            padding-bottom: 5px;
        }
        label{
            font-size: 20px;
        }
        .button{
            display:flex;
            justify-content: center;
            button{
                font-size: 20px;
            }
        }
    }
}
</style>