<template>
  <div id="app">

    <!-- Navbar  -->
    <div>
      <b-navbar toggleable="lg" type="light" variant="fadded">
        <b-navbar-brand href="#">
          <img
            src="./assets/static/images/logo.webp"
            width="80px"
            id="logo"
            alt="logo"
        /></b-navbar-brand>

        <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

        <b-collapse id="nav-collapse" is-nav>
          <b-navbar-nav>
            <b-nav-item to="/" > <h4>Home</h4> </b-nav-item>
            <b-nav-item to="/about"><h4>About</h4> </b-nav-item>
            <b-nav-item href="#"><h4>Contact</h4> </b-nav-item>
            <b-nav-item href="#"><h4>Donate</h4> </b-nav-item>
            <b-nav-item href="#"><h4>FAQ's</h4> </b-nav-item>
            <b-nav-item v-bind:to="myaccount" v-if="authenticated"><h4>My account</h4> </b-nav-item>
            <h4>
              <b-nav-item-dropdown text="User" right>

                <b-dropdown-item v-if="authenticated" @click="logout">Logout</b-dropdown-item>
                <div v-else>
                <b-dropdown-item @click='link="candidate";login_visibility=!login_visibility'>Candidate login</b-dropdown-item>
                <b-dropdown-item @click='link="manager";login_visibility=!login_visibility'>Admin login</b-dropdown-item>
                </div>
              </b-nav-item-dropdown>
            </h4>
          </b-navbar-nav>

          <!-- Right aligned nav items -->
          <b-navbar-nav class="ml-auto">
            <b-nav-form>
              <b-form-input
                size="md"
                class="mr-md-2"
                placeholder="Search"
              ></b-form-input>
              <b-button
                size="md"
                class="my-2 my-md-0"
                variant="primary"
                type="submit"
                >Search</b-button
              >
            </b-nav-form>
          </b-navbar-nav>
        </b-collapse>
      </b-navbar>
    </div>
    <!-- Navbar ends  -->

    <!-- login form  -->
     <transition name="fade">
    <Login v-bind:link="link" v-show="login_visibility" @loggedin="logged_in"  @login_cancel="login_visibility=!login_visibility"/>
     </transition>
    <router-view></router-view>

    <!-- Footer component  -->
  </div>
</template>

<script>
import Login from './components/Login.vue';
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
  name:"App",
  components:{
    Login
  },
  data(){
    return {
      BASE_URL:"/",
      myaccount:"",
      authenticated:false,
      login_visibility:false,
      link:"candidate"
    }
  },
  methods:{
    logout:function(){
      if(this.$cookies.isKey('authtoken')){
        this.authenticated=false;
        this.myaccount="";
        let authtoken=this.$cookies.get('authtoken');
        this.$cookies.remove('authtoken');
        let vm=this;
        axios.get(this.BASE_URL+'logout',{params:{'authtoken':authtoken}}).then(res=>{
          if(res.data.Error){
            vm.$router.push('/');
          }
          vm.$router.push('/');
        })
      }
    },
    logged_in:function(){
      if(this.$cookies.isKey('authtoken')){ 
      let authtoken=this.$cookies.get("authtoken");
      let vm=this;
      axios.get(this.BASE_URL+'checksession',{params:{'authtoken':authtoken}}).then(res=>{
        if(res.data.Error){
          vm.myaccount="";
          vm.authenticated=false;
        }
        else{
          vm.authenticated=true;
          if(res.data.user=='candidate'){
            vm.myaccount="/candidate";
            vm.$router.push('/candidate')
          }
        }
    })}
    }
  },
  mounted:function(){
    if(this.$cookies.isKey('authtoken')){ 
      let authtoken=this.$cookies.get("authtoken");
      let vm=this;
      axios.get(this.BASE_URL+'checksession',{params:{'authtoken':authtoken}}).then(res=>{
        if(res.data.Error){
          vm.myaccount="";
          vm.authenticated=false;
        }
        else{
          vm.authenticated=true;
          if(res.data.user=='candidate'){
            vm.myaccount="/candidate";
            vm.$router.push('/candidate')
          }
        }
    })}
  }
}
</script>

