<template>
  <div @click.right="prevent($event)">
      
              <transition appear name="fade">
      <b-container>
          <b-row align-h="center">
              <b-col align-h="center" class="video_container">
                  <video v-bind:src="main_video" preload="metadata" id="main_video" controls controlslist="nodownload"></video>
              </b-col>
          </b-row>
      </b-container>
              </transition>
      <b-container fluid class="prev_video_container">
          <b-row>
              <b-col>
                  <h2 class="main_title">Previous Videos</h2>
              </b-col>
          </b-row>
          <b-row class="prev_video_card_container border-bottom border-dark my-2" v-for="video in videos" :key="video.title">
              <b-col md="12"><h4 style="text-decoration:underline;" class="text-center border-secondary py-2">{{video.title}}</h4></b-col>
              <b-col md="3" class="prev_video_card" align-h="center" @click="play_vid($event)" v-for="vid in video.videos" :key="vid.title">
                  <video v-bind:src="vid.video|genUrl" class="prev_video" preload="metadata"></video>
                  <div class="prev_video_desc">
                  <h4>{{vid.title}}</h4> </div>
              </b-col>
          </b-row>

      </b-container>
  </div>
</template>

<script>
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
    name:"Lecture",
    filters:{
        genUrl:function(url){
            let postUrl=url.split('/');
            postUrl.shift();
            return "/"+postUrl.join("/");
        }
    },
    data(){
        return {
            BASE_URL:"/",
            main_video:"",
            //To be fetched form the server
            videos:[]
        }
    },
    methods:{
        play_vid:function(e){
            this.main_video=e.target.getAttribute('src');
        },
        prevent:function(e){
            e.preventDefault();
            return false;
        }
    },
    mounted:function(){
        let vm=this;
        axios.get(this.BASE_URL+'candidate/lectures',{params:{"authtoken":this.$cookies.get('authtoken')}}).then(res=>{
            if(res.data.Error)
            {
                window.alert(res.data.Error);
                console.log(res.data);
                vm.$router.push('/');
            }
            else{
                vm.videos=res.data.data;
                if(res.data.data.length!=0){
                    let cur_link=res.data.data[0].videos[0].video;
                    cur_link=cur_link.substring(1);
                    vm.main_video=vm.BASE_URL+cur_link;
                }
            }
        })
    }
}
</script>

<style scoped lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
.video_container{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}
#main_video{
    margin: auto;
    width: 100vw;
    max-width: 800px;
    max-height: 600px;
    min-width:300px;
}
.prev_video_container{
    margin-top:20px ;
    .prev_video_card_container{
        display: flex;
        justify-content: space-around;
    }
    .main_title{
        text-align: center;
        color: black;
        border-top: 2px solid black;
        font-family: 'Playfair Display', serif !important;
    }
    .prev_video_card{
        margin: 20px 5px;
        padding: 10px 2px;
        display:flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .prev_video_desc{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-direction: column;
            h4{
                margin-right: 5px;
                font-size: 1.4rem;
            }
        }
        &:hover{
            box-shadow: 0px 0px 5px 1px black;
            cursor: pointer;
        }
        
    }
    .prev_video{
        width: 250px;
        border-radius: 5px;
    }
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to {
  opacity: 0;
}
</style>