<template>
  <div id="profile" class="my-3">
      <div class="form_container">
          <h2 class="text-center profile-title">Welcome <b> {{formdata.name}} </b></h2>
          <h3 class="text-center profile-title">Level: <b>{{formdata.level}}</b></h3>
          <b-container class="my-3 py-3">
    <b-form @submit="register" @reset="reset">
      <b-form-group
        id="name-group"
        label="Name:"
        label-for="name"
      >
        <b-form-input
          id="name"
          v-model="formdata.name"
          placeholder="Enter your name.."
          required
        ></b-form-input>
      </b-form-group>
      <b-form-group
        id="password-group"
        label="Password:"
        label-for="password"
      >
        <b-form-input
          id="password"
          v-model="formdata.password"
          placeholder="Enter your password.."
          required
        ></b-form-input>
      </b-form-group>
<b-form-group label="Gender:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-model="formdata.gender" style="float:left" :aria-describedby="ariaDescribedby" name="Gender" value="M">Male</b-form-radio>
      <b-form-radio v-model="formdata.gender" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="Gender" value="F">Female</b-form-radio>
    </b-form-group>
      <b-form-group id="email-group" label="Your Email:" label-for="email">
        <b-form-input
          id="email"
          type="email"
          v-model="formdata.email"
          placeholder="Enter your email.."
          required
        ></b-form-input>
      </b-form-group>
            <b-form-group
        id="phone-group"
        label="Phone:"
        type="tel"
        label-for="phone"
      >
        <b-form-input
          id="phone"
          v-model="formdata.phone"
          placeholder="Contact(whatsapp).."
          required
        ></b-form-input>
      </b-form-group>
      <b-form-group>
         <label for="dob">Date of birth:</label>
    <b-form-datepicker v-model="formdata.dob" id="dob"  class="mb-2"></b-form-datepicker>
      </b-form-group>
      <b-form-group label="Occupation:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-model="formdata.occupation" style="float:left" :aria-describedby="ariaDescribedby" name="occupation" value="S">Student</b-form-radio>
      <b-form-radio v-model="formdata.occupation" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="occupation" value="E">Employee</b-form-radio>
      <b-form-radio v-model="formdata.occupation" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="occupation" value="B">Business</b-form-radio>
    </b-form-group>
      <b-form-group label="Maritial Status:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-model="formdata.married" style="float:left" :aria-describedby="ariaDescribedby" name="married" value="M">Married</b-form-radio>
      <b-form-radio v-model="formdata.married" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="married" value="U">Unmarried</b-form-radio>
    </b-form-group>
          <b-form-group
        id="country-group"
        label="Country:"
        label-for="country"
      >
        <b-form-input
          id="country"
          placeholder="Country.."
          v-model="formdata.country"
          required
        ></b-form-input>
      </b-form-group>
          <b-form-group
        id="city-group"
        label="City:"
        label-for="city"
      >
        <b-form-input
          id="city"
          placeholder="City.."
          v-model="formdata.city"
          required
        ></b-form-input>
      </b-form-group>
    <b-row>
      <b-col> <b-button type="reset" pill block variant="secondary">Cancel</b-button></b-col>
      <b-col> <b-button type="submit" pill block variant="success">Save</b-button></b-col>
      </b-row>
    </b-form>
  </b-container>
      </div>
  </div>
</template>

<script>
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
name:"Profile",
data(){
  return{
    BASE_URL:"/",
    formdata:{
      name:null,
      password:null,
      gender:null,
      email:null,
      phone:null,
      dob:null,
      occupation:null,
      married:null,
      country:null,
      city:null,
      level:null
    }
  }
},
methods:{
  register:function(e){
    e.preventDefault();
    let vm=this;
    let params=this.formdata;
    params['authtoken']=this.$cookies.get('authtoken');
   axios.post(this.BASE_URL+'candidate/profile',params).then(res=>{
     if(res.data.Error)
     {
       window.alert(res.data.Error);
       vm.$router.push('/');
     }
     else{
       window.alert('updated sucessfully');
       vm.formdata=res.data.candidate;
     }
   })
  },
  reset:function(e){
    e.preventDefault();
    this.$router.push('/candidate');
  }
},
mounted:function(){
  let vm=this;
  axios.get(this.BASE_URL+'candidate/profile',{params:{"authtoken":this.$cookies.get('authtoken')}}).then(res=>{
    if(res.data.Error){
      vm.$router.push('/');
    }
    vm.formdata=res.data.candidate;
  })
}
}
</script>

<style scoped lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap');
.profile-title{
 font-family: 'Playfair Display', serif !important;
 color: #556EE6;
 font-weight: 900;
}

</style>