/* eslint-disable */
<template>
  <div id="register">
      <div class="form_container">
          <h2>Register for 7 days <b class="text-success">FREE</b> online seminar</h2>
          <h5>Every Saturday from 7:30 PM to 8:45 PM</h5>
          <div>
    <b-form @submit="register">
      <b-form-group
        id="name-group"
        label="Name:"
        label-for="name"
      >
        <b-form-input
          id="name"
          v-model="formdata.name"
          placeholder="Enter your name.."
          required
          v-bind:readonly="verified"
        ></b-form-input>
      </b-form-group>
<b-form-group label="Gender:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-bind:readonly="verified" v-model="formdata.gender" style="float:left" :aria-describedby="ariaDescribedby" name="Gender" value="M">Male</b-form-radio>
      <b-form-radio v-bind:readonly="verified" v-model="formdata.gender" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="Gender" value="F">Female</b-form-radio>
    </b-form-group>
      <b-form-group id="email-group" label="Your Email:" label-for="email">
        <b-form-input
          id="email"
          type="email"
          v-model="formdata.email"
          placeholder="Enter your email.."
          required
          v-bind:readonly="verified"
        ></b-form-input>
      </b-form-group>
            <b-form-group
        id="phone-group"
        label="Phone:"
        type="tel"
        label-for="phone"
      >
        <b-form-input
          id="phone"
          v-model="formdata.phone"
          placeholder="Contact(whatsapp).."
          required
          v-bind:readonly="verified"
        ></b-form-input>
      </b-form-group>
      <b-form-group>
         <label for="dob">Date of birth:</label>
    <b-form-datepicker v-bind:readonly="verified" v-model="formdata.dob" id="dob"  class="mb-2"></b-form-datepicker>
      </b-form-group>
      <b-form-group label="Occupation:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-bind:readonly="verified" v-model="formdata.occupation" style="float:left" :aria-describedby="ariaDescribedby" name="occupation" value="S">Student</b-form-radio>
      <b-form-radio v-bind:readonly="verified" v-model="formdata.occupation" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="occupation" value="E">Employee</b-form-radio>
      <b-form-radio v-bind:readonly="verified" v-model="formdata.occupation" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="occupation" value="B">Business</b-form-radio>
    </b-form-group>
      <b-form-group label="Maritial Status:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-bind:readonly="verified" v-model="formdata.married" style="float:left" :aria-describedby="ariaDescribedby" name="married" value="M">Married</b-form-radio>
      <b-form-radio v-bind:readonly="verified" v-model="formdata.married" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="married" value="U">Unmarried</b-form-radio>
    </b-form-group>
      <b-form-group label="Are you already connected to Iskcon anywhere:" v-slot="{ ariaDescribedby }">
      <b-form-radio v-bind:readonly="verified" v-model="formdata.connected" style="float:left" :aria-describedby="ariaDescribedby" name="connected" value="Y">Yes</b-form-radio>
      <b-form-radio v-bind:readonly="verified" v-model="formdata.connected" style="float:left;margin-left:30px" :aria-describedby="ariaDescribedby" name="connected" value="N">No</b-form-radio>
    </b-form-group>
          <b-form-group
        id="country-group"
        label="Country:"
        label-for="country"
      >
        <b-form-input
          id="country"
          placeholder="Country.."
          v-model="formdata.country"
          required
          v-bind:readonly="verified"
        ></b-form-input>
      </b-form-group>
          <b-form-group
        id="city-group"
        label="City:"
        label-for="city"
      >
        <b-form-input
          id="city"
          placeholder="City.."
          v-model="formdata.city"
          required
          v-bind:readonly="verified"
        ></b-form-input>
      </b-form-group>
      <b-form-group label="
How you came to know about these sessions? (FB, YouTube, WhatsApp or other.)*">
        <b-form-input list="recommend" v-bind:readonly="verified" v-model="formdata.source" required></b-form-input>

    <datalist id="recommend">
      <option>Youtube</option>
      <option>whatsapp</option>
      <option>Facebook</option>
      <option>Instagram</option>
      <option>Twitter</option>
      <option>Friends</option>
    </datalist>
      </b-form-group>
      <b-form-group label="Any other specific information or remark:
">
<b-form-textarea
      id="anyinfo"
      placeholder="Enter something..."
      rows="3"
      v-model="formdata.info"
      max-rows="6"
      v-bind:readonly="verified"
    ></b-form-textarea></b-form-group>
          <b-form-group v-if="verified"
        id="otp-group"
        label="OTP:"
        label-for="otp"
      >
        <b-form-input
          id="otp"
          v-model="formdata.otp"
          placeholder="Enter otp sent at your mail.."
        ></b-form-input> </b-form-group>
      <b-button type="submit" pill block variant="success">Register</b-button>
      
    </b-form>
  </div>
      </div>
  </div>
</template>

<script>
import Vue from "vue";
import axios from 'axios';
import VueAxios from 'vue-axios';
import VueCookies from 'vue-cookies';

Vue.use(VueCookies);
Vue.use(VueAxios, axios);

export default {
name:"Register",
data(){
  return{
    verified:false,
    BASE_URL:"/",
    formdata:{
      name:null,
      gender:null,
      email:null,
      phone:null,
      dob:null,
      occupation:null,
      married:null,
      connected:null,
      country:null,
      city:null,
      source:null,
      info:null,
      otp:null,
      authtoken:null
    }
  }
},
methods:{
  register:function(e){
    e.preventDefault();
    if(!this.verified){
      let vm=this;
      axios.post(this.BASE_URL+'candidate/verify',this.formdata).then(res=>{
        // Form validation if required
        if(res.data.Error){
          window.alert(res.data.Error);
        }
        else{
          vm.verified=true;
          vm.formdata.authtoken=res.data.authtoken;
        }
      })
    }
    else{
      let vm=this;
      axios.post(this.BASE_URL+'candidate/register',this.formdata).then(res=>{
        if(res.data.Error){
          window.alert(res.data.Error);
        }
        else{
          window.alert("Registration successfull");
          vm.$router.push('/');
        }
      })
    }
  }
}
}
</script>

<style scoped lang="scss">
a:hover{
    text-decoration: unset;
}
#register{
    position: relative;
    background:rgba(33,32,156,0.6);
    min-height: 500px;
    padding: 10px;
    .form_container{
        margin: 10px auto;
        max-width: 800px;
        background: white;
        padding: 20px 40px;
        border-radius: 20px;
        box-shadow: 0px 0px 10px 2px black;
        h5{
            border-bottom:1px solid black;
            margin-bottom: 20px;
        }
    }
}
#register::after{
    content: '';
    position: absolute;
    background:url('../assets/images/audience.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    z-index: -1;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
}

</style>